describe('Jest Setup Verification', () => {
  it('should support Jest matchers', () => {
    const mockFn = jest.fn();
    mockFn('test');
    
    expect(mockFn).toHaveBeenCalled();
    expect(mockFn).toHaveBeenCalledWith('test');
  });
  
  it('should support async testing', async () => {
    const promise = Promise.resolve('test data');
    await expect(promise).resolves.toBe('test data');
  });
  
  it('should support mocking', () => {
    const mockObject = {
      method: jest.fn().mockReturnValue('mocked value')
    };
    
    expect(mockObject.method()).toBe('mocked value');
    expect(mockObject.method).toHaveBeenCalledTimes(1);
  });
});